<template>
  <div class="page page-task-list">
    <div class="index-main bg-net" style="border-top: 1px solid #efefef">
      <div class="index-main-section v-py-md">
        <a-breadcrumb>
          <a-breadcrumb-item href="">首页</a-breadcrumb-item>
          <a-breadcrumb-item href="">实训平台</a-breadcrumb-item>
          <a-breadcrumb-item href="">任务管理</a-breadcrumb-item>
          <a-breadcrumb-item href="">创建任务</a-breadcrumb-item>
        </a-breadcrumb>
      </div>
      <div class="index-main-section v-pb-lg bg-white border-radius v-pa-md">
        <a-divider orientation="left"> 任务基本信息 </a-divider>
        <div class="task-form" style="padding: 0 50px">
          <div class="form-field">
            <span class="form-field-label">选择资源：</span>
            <div class="form-field-content">
              <a-select :default-value="form.proj" v-model="form.proj" style="width: 500px">
                <a-select-option value="资源名称1">资源名称1</a-select-option>
                <a-select-option value="资源名称2">资源名称2</a-select-option>
                <a-select-option value="资源名称3">资源名称3</a-select-option>
                <a-select-option value="资源名称4">资源名称4</a-select-option>
                <a-select-option value="资源名称5">资源名称5</a-select-option>
              </a-select>
            </div>
          </div>
          <div class="form-field">
            <span class="form-field-label">任务名称：</span>
            <div class="form-field-content">
              <a-input v-model="form.name" placeholder="输入任务名称" style="width: 500px"></a-input>
            </div>
          </div>
          <div class="form-field">
            <span class="form-field-label">任务用时：</span>
            <div class="form-field-content">
              <a-input v-model="form.limit" placeholder="输入用时" suffix="分钟" style="width: 180px"></a-input>
            </div>
          </div>

          <a-alert type="warning" class="v-mb-md">
            <div slot="message">
              <a-icon type="exclamation-circle" style="font-size: 16px; margin-right: 6px; color: #faad14" />温馨提示： 上传评分表前，请检查评分表中定义模块与技能点是否与系统制定一致，<span
                class="text-underline"
                >点击查看</span
              >系统制定模块与技能点。
            </div>
          </a-alert>
          <div class="form-field">
            <span class="form-field-label">评分表：</span>
            <div class="form-field-content">
              <div class="flex items-center">
                <a-upload action="https://www.mocky.io/v2/5cc8019d300000980a055e76" list-type="picture-card" :file-list="fileList" @preview="handlePreview" @change="handleUploadChange">
                  <div v-if="fileList.length < 8">
                    <a-icon type="plus" />
                    <div class="ant-upload-text">点击上传</div>
                  </div>
                </a-upload>
              </div>
            </div>
          </div>

          <a-alert type="warning" class="v-mb-sm">
            <div slot="message">
              <a-icon type="exclamation-circle" style="font-size: 16px; margin-right: 6px; color: #faad14" />温馨提示：
              是否收费表示是否需要输入设备码解锁任务，是：表示需要输入产品码才可答题，否：表示无需输入即可答题
            </div>
          </a-alert>
          <div class="form-field">
            <span class="form-field-label">是否收费：</span>
            <div class="form-field-content">
              <a-switch style="margin-top: 5px" checked-children="是" un-checked-children="否" default-checked v-model="form.cost" />
            </div>
          </div>
        </div>

        <a-divider orientation="left"> 分配选手 </a-divider>
        <div class="task-form">
          <div class="form-field">
            <span class="form-field-label"></span>
            <div class="form-field-content">
              <a-transfer
                :data-source="mockData"
                :titles="['未分配选手', '已分配选手']"
                :target-keys="targetKeys"
                :selected-keys="selectedKeys"
                :render="(item) => item.title"
                :disabled="disabled"
                :list-style="{
                  width: '250px',
                  height: '300px',
                }"
                @change="handleAssignChange"
                @selectChange="handleAssignSelectChange"
              />
            </div>
          </div>
          <div class="form-field">
            <span class="form-field-label"></span>
            <div class="form-field-content">
              <a-button type="primary" @click="onSubmit" style="width: 100px" :loading="saving"> 保存 </a-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    const mockData = []
    for (let i = 0; i < 20; i++) {
      mockData.push({
        key: i.toString(),
        title: `选手${i + 1}`,
        description: `选手描述${i + 1}`,
      })
    }
    const oriTargetKeys = mockData.filter((item) => +item.key % 3 > 1).map((item) => item.key)
    return {
      mockData,
      targetKeys: oriTargetKeys,
      selectedKeys: ['1', '4'],
      labelCol: { span: 2 },
      wrapperCol: { span: 10 },
      form: {
        proj: '',
        name: '',
        limit: undefined,
        cost: true,
      },
      fileList: [],
      saving: false,
    }
  },
  methods: {
    load() {
      let list = []
      for (let index = 1; index < 20; index++) {
        list.push({
          id: index,
          title: 'B模块 嵌入式编程' + index,
          people: 1345,
        })
      }
      this.list = list
    },
    onTabChange(key) {
      document.querySelector('#' + key).scrollIntoView(true)
    },
    handleUploadChange(info) {
      const status = info.file.status
      if (status !== 'uploading') {
        console.log(info.file, info.fileList)
      }
      if (status === 'done') {
        this.$message.success(`${info.file.name} file uploaded successfully.`)
      } else if (status === 'error') {
        this.$message.error(`${info.file.name} file upload failed.`)
      }
      this.fileList = info.fileList
    },
    handlePreview() {},
    onSubmit() {
      this.saving = true
      setTimeout(() => {
        this.saving = false
      }, 1200)
    },
    handleAssignChange(nextTargetKeys, direction, moveKeys) {
      this.targetKeys = nextTargetKeys
      console.log('targetKeys: ', nextTargetKeys)
      console.log('direction: ', direction)
      console.log('moveKeys: ', moveKeys)
    },
    handleAssignSelectChange(sourceSelectedKeys, targetSelectedKeys) {
      this.selectedKeys = [...sourceSelectedKeys, ...targetSelectedKeys]

      console.log('sourceSelectedKeys: ', sourceSelectedKeys)
      console.log('targetSelectedKeys: ', targetSelectedKeys)
    },
  },
  mounted() {
    this.load()
  },
}
</script>

<style lang="stylus"></style>
